*{
    margin: 0%;
    padding: 0%;
}
.whole-box{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: aliceblue;
}
.card{
    width: 26vw;
    height: 92vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 12px;
    background-color: blanchedalmond;
    box-shadow: 0px 0px 20px 1px black;
}
.input-button{
    margin: 20px 0px;
}
.card .button-add{
    height: 55px;
    background-color: white;
    color: green;
    border-radius: 0;
}
.card .button-add:hover{
    height: 55px;
    background-color: green;
    color: white;
    border-radius: 0;
}
.task .button-delete{
    background-color: white;
    color: red;
    height: 43px;
}
.task .button-delete:hover{
    background-color: red;
    color: white;
}

.card .input{
    background-color: aliceblue;
    font-style: italic;
    font-weight: 800;
    text-align: center;
}

.task{
    display: flex;
    align-items: center;
    width: 270px;
    padding: 20px;
    
}
.task p{
    font-size: 1.5rem;
    padding: 0px 10px;
    /* opacity: 0.6; */
    font-weight: 500;
    font-style: italic;
    text-transform: capitalize;
}
.all-tasks{
    overflow-y: auto;
    height: 480px;
}

@media(max-width:480px){
    .whole-box{
        height: 92vh;
    }
    .card{
        width: 90vw;
        height: 80vh;
    }
}